import { myFirebase } from '../firebase/firebase';
import * as firebase from 'firebase/app';

export const LOGIN_REQUEST = 'LOGIN_REQUEST';
export const LOGIN_SUCCESS = 'LOGIN_SUCCESS';
export const LOGIN_FAILURE = 'LOGIN_FAILURE';

export const LOGOUT_REQUEST = 'LOGOUT_REQUEST';
export const LOGOUT_SUCCESS = 'LOGOUT_SUCCESS';
export const LOGOUT_FAILURE = 'LOGOUT_FAILURE';

export const VERIFY_REQUEST = 'VERIFY_REQUEST';
export const VERIFY_SUCCESS = 'VERIFY_SUCCESS';

export const START_WAIT = 'START_WAIT';
export const STOP_WAIT = 'STOP_WAIT';

const requestLogin = () => {
    return {
        type: LOGIN_REQUEST
    };
};

const receiveLogin = (user) => {
    return {
        type: LOGIN_SUCCESS,
        user,
    };
};

const loginError = () => {
    return {
        type: LOGIN_FAILURE
    };
};

const requestLogout = () => {
    return {
        type: LOGOUT_REQUEST
    };
};

const receiveLogout = () => {
    return {
        type: LOGOUT_SUCCESS
    };
};

const logoutError = () => {
    return {
        type: LOGOUT_FAILURE
    };
};

const verifyRequest = () => {
    return {
        type: VERIFY_REQUEST
    };
};

const verifySuccess = () => {
    return {
        type: VERIFY_SUCCESS
    };
};

var provider = new firebase.auth.GoogleAuthProvider();

export const loginUser = () => dispatch => { // (email, password)
    dispatch(requestLogin());

    myFirebase.auth().signInWithRedirect(provider);
    myFirebase.auth().getRedirectResult().then(function(result) {
        /*
        if(result.credential) {
            var token = result.credential.accessToken;
        }
        */
        var user = result.user;
        if (user) {
            dispatch(receiveLogin(result.user));
        }
    }).catch(function() {
        dispatch(loginError());
        console.log('Error Logging In With Google. tbh, idk how this happened.');
    })
};

export const logoutUser = () => dispatch => {
    dispatch(requestLogout());
    myFirebase
        .auth()
        .signOut()
        .then(() => {
            dispatch(receiveLogout());
        })
        .catch(err => {
            dispatch(logoutError());
        });
};

export const verifyAuth = () => dispatch => {
    dispatch(verifyRequest());
    myFirebase.auth().onAuthStateChanged(user => {
        if (user !== null) {
            dispatch(receiveLogin(user));
        }
        dispatch(verifySuccess());
    });
};

